//custom
.mm{
    &-navbar{
        height: 60px;
    }

    &-navbar &-btn{
        top: 50%;
        .translate(0, -50%);
    }

    &-title{
        text-transform: uppercase;
        color: #ffffff;
        .font-bold;
        font-size: 2rem;
        padding:0;
        line-height: 1;
        position: absolute;
        top: 50%;
        left: 50%;
        .translate(-50%, -50%);
    }
    &-menu > &-panel{
        >.mm-list:first-child,
        >.mm-navbar+.mm-listview{
            margin-top: 0;
        }
    }
}

.mm-panel{

    .primary-nav__mobile-btn-close {
        position: absolute;
        top: 0;
        right: 0;
        padding: 0 40px;
        color: #ffffff;
        font-size: 2rem;
    }
    
    &:first-child {
        .primary-nav__mobile-btn-close {
            float: left;
        }
    }
}
