/* Arrows */
.slick-prev,
.slick-next
{
    font-size: 0;
    line-height: 0;
    display: inline-block;
    padding: 0;
    width: 6.6rem !important;
    height: 1.5rem;
    cursor: pointer;
    position: relative;
    top: 0;
    color: transparent;
    border: none;
    outline: none;
    background: transparent;     

}
.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus
{
    color: transparent;
    outline: none;
    background: transparent;
}
.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before
{
    opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before
{
    opacity: .25;
}

.slick-prev:before,
.slick-next:before
{
    font-family: 'slick';
    font-size: 20px;
    line-height: 1;

    opacity: .75;
    color: white;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.slick-next:before
{
    content: '';
    background-image: url('../imgs/details/arrow-down.svg');
    background-repeat: no-repeat;
    background-size: cover;
    width: 1rem;
    height: 1rem;
    display: inline-block;
    fill: @white;
    opacity: 1;
    background-position: center center;
}
.slick-prev:before
{
    content: '';
    background-image: url('../imgs/details/arrow-up.svg');
    background-repeat: no-repeat;
    background-size: cover;
    width: 1rem;
    height: 1rem;
    display: inline-block;
    fill: @white;
    opacity: 1;
    background-position: center center;
}

[dir='rtl'] .slick-next:before
{
    content: '';
}

/* Dots */
.slick-slider
{
    //margin-bottom: 30px;
}

.slick-dots
{
    position: absolute;
    //bottom: -45px;
    top: 0;
    right: 0;

    display: block;

    //width: 100%;
    padding: 0;

    list-style: none;
    text-align: center;
}
.slick-dots li
{
    position: relative;

    display: inline-block;

    width: 20px;
    height: 20px;
    //margin: 0 5px;
    padding: 0;

    cursor: pointer;
}
.slick-dots li button
{
    font-size: 0;
    line-height: 0;

    display: block;

    width: 14px;
    height: 14px;
    padding: 5px;

    cursor: pointer;

    //color: transparent;
    border: 0;
    outline: none;
    background: @white;
    border-radius: 50%;
}
.slick-dots li button:hover,
.slick-dots li button:focus
{
    outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before
{
    opacity: 1;
}
.slick-dots li button:before
{
    font-family: 'slick';
    font-size: 6px;
    line-height: 20px;

    position: absolute;
    top: 0;
    left: 0;

    width: 20px;
    height: 20px;
    background-color: @white;
    border-radius: 50%;

    //content: '';
    text-align: center;

    opacity: .25;
    color: black;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before
{
    opacity: .75;
    color: black;
}

.modal-main {

    .slick-next,
    .slick-prev {
        position: absolute;
        z-index: 3;
        bottom: 0;
        height: 100%;
        background: rgba(255, 255, 255, 0.8);

        width: 2.6rem !important;

        @media(min-width: @sm-min) {
            visibility: hidden;
        }
            

        &:before {
            content: "";
            transform: rotate(-90deg);
        }
    }

    .slick-next {
        right: 0;
    }

    .slick-prev {
        left: 0;
    }
}
